import { useSearchParams } from "next/navigation";

const SearchPage = () => {
  const searchParams = useSearchParams(); // searchParams obyekti orqali URL'dan qiymatni olish
  const query = searchParams.get("query"); // "query" parametrining qiymatini olish

  console.log(query);

  return (
    <div>
      <h1 className="text-2xl font-bold text-center">{query || "No query provided"}</h1>
    </div>
  );
};

export default SearchPage;
